
.UI-he {
    background-color: #f0f0f0;
    position: fixed;
    display: block;
    top: 0;
    right: 0;
    width: 25rem;
    height: 100%;
    padding: 0 2%;
    z-index: -1;
    overflow-y: scroll;
    text-align: right;
}

.UI-btn-out-he {
    position: fixed;
    top: 2%;
    right: 2%;
    border-radius: 100%;
    width: 4rem;
    box-shadow: gray 3px 3px 6px;
    cursor: pointer;
}

.UI-btn-in-he {
    position: relative;
    display: block;
    top: 2%;
    right: -18rem;
    border-radius: 100%;
    width: 4rem;
    box-shadow: none;
    cursor: pointer;
    margin-bottom: 1rem;
}

.btn-dropdown-he {
    background-color: black;
    position: absolute;
    top: 1rem;
    left: 0;
}

.dropdown-menu-he {
    margin-top: 1rem;
    width: 17rem;
    text-align: right;
    padding: 3rem;
}

.dropdown-item-he {
    margin-right: 0;
}

.List-edit-form-he {
    margin-top: -0.85rem;
}

.List-edit-form-he input {
    width: 10rem;
    border: none;
    background: #fff;
    padding: 0 1.5em;
    font-size: initial;
}

.List-edit-form-he button {
    position: absolute;
    margin-top: 0.1rem;
    font-size: 1.5rem;
    border: none;
    background: transparent;
    color: black;
    font-weight: bold;
    margin-right: 1.5rem;
    cursor: pointer;
}

.List-edit-form-he input,
.List-edit-form-he button {
    font-family: "Quicksand", sans-serif;
    height: 3rem;
}